/* ============================================= */
/* =====    (Bottom Navigation Bar) ===== */
/* ============================================= */

/*        */
.bottom-nav {
  /*       */
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;

  /*     */
  width: 100%;
  /*    */
  height: 64px;

  /*         */
  z-index: 120;

  /*     flexbox  */
  display: flex;
}

/*        */
.bottom-nav-inner {
  /* (.bottom-nav)     */
  flex: 1;
  height: 64px;

  /*    */
  background: rgba(255, 255, 255, 0.96);
  /* iOS   ()  */
  /* iOS/Android       */
  -webkit-backdrop-filter: blur(16px);
  backdrop-filter: blur(16px);

  /*     */
  border: 1px solid rgba(255, 255, 255, 0.4);
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.15);

  /*     */
  border-radius: 16px 16px 0 0;
  /*           */
  overflow: hidden;             /*  :  /     */
  background-clip: padding-box; /*  /  */
  mask-image: radial-gradient(circle at center, #fff 99%, transparent 100%);
  -webkit-mask-image: -webkit-radial-gradient(white, black); /* WebKit    */
  transform: translateZ(0);     /*     */
  isolation: isolate;           /*       */

  /*  ()  */
  display: flex;
  align-items: center; /*    */
  justify-content: space-around; /*     */

  /*   (, , ) */
  padding: 12px 8px 10px;
  /*    */
  gap: 0;
}

/* iPhone   (Home Bar)  ' '    */
@supports (height: env(safe-area-inset-bottom)) {
  .bottom-nav-inner {
    /*  UI          */
    padding-bottom: calc(10px + env(safe-area-inset-bottom));
    height: calc(64px + env(safe-area-inset-bottom));
  }
}

/*     ()  */
.bottom-nav-item {
  /*    */
  flex: 1;

  /*     */
  background: none;
  border: none;

  /*      flexbox  */
  display: flex;
  flex-direction: column; /*    */
  align-items: center;    /*    */
  justify-content: flex-start; /*   */
  gap: 4px; /*     */

  /*        */
  padding: 0;
  cursor: pointer;

  /*    ( ) */
  color: #A0A0A0;
  font-size: 12px;
  line-height: 14px;
  font-weight: 600;
  letter-spacing: -0.02em;

  /* ::after       */
  position: relative;
}

/*  (  )      */
.bottom-nav-item:active {
  transform: translateY(1px);
}

/*   SVG    */
.bottom-nav-item svg {
  display: block;
}

/* SVG    (   ) */
.bottom-nav-item .icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  /*        */
  height: 24px;
}

/*     */
.bottom-nav-item.active {
  /*        */
  color: transparent;
  /*    */
  background: linear-gradient(120deg, #448FFF 0%, #4833D0 100%);
  /*         */
  -webkit-background-clip: text;
  background-clip: text;
}

/*    SVG     */
/* SVG   <defs><linearGradient id="nav-gradient">...</linearGradient></defs>    */
.bottom-nav-item.active svg path,
.bottom-nav-item.active svg rect,
.bottom-nav-item.active svg circle {
  fill: url(#nav-gradient);
  stroke: none;
}

/*     */
.bottom-nav-label {
  font-size: 12px;
  font-weight: 600;
}


/* ============================================= */
/* =====     (Helper Class) ===== */
/* ============================================= */

/*            */
.has-bottom-nav {
  /*             */
  padding-bottom: 88px;
}

/*          */
@supports (height: env(safe-area-inset-bottom)) {
  .has-bottom-nav {
    padding-bottom: calc(88px + env(safe-area-inset-bottom));
  }
}